<template>
<div>

</div>
</template>

<script>
export default {
  name: 'HouseDetail',
  data () {
    return {
      beginTime: '',
      endTime: '',
      sentTime: false,
      cityPyName: '',
      houseId: '',
      houseDetails: []
    }
  },
  watch: { },
  created () {
    const that = this
    that.beginTime = new Date().getTime()
    that.cityPyName = this.$route.path.split('/')[2]
    that.houseId = this.$route.path.split('/')[4]
    that.$http.get('/zufang/' + that.cityPyName + '/f/' + that.houseId, {
      params: {},
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(function (response) {
        if (response.data.errorCode === 200) {
          console.log(response.data.data)
          that.houseDetails = response.data.data
          if (response.data.data.usId !== undefined && response.data.data.usId !== '') {
            that.sentTime = true
          }
        }
      })
      .catch(function (error) {
        console.log(error)
      })
      .then(function () {
      })
  },
  beforeDestroy () {
    const that = this
    that.endTime = new Date().getTime()
    const stayTime = that.endTime - that.beginTime
    console.log(stayTime)
    this.$notify.info({
      title: '停留时间',
      message: (stayTime / 1000) + '秒'
    })
    that.$http.post('/page_on_time', {
      usId: that.houseDetails.usId,
      startTime: that.beginTime,
      endTime: that.endTime,
      pageOnTime: stayTime
    }, {headers: {'Content-Type': 'application/json'}})
      .then(function (response) {
        console.log(response)
      })
      .catch(function (error) {
        alert(error)
      })
  }
}
</script>

<style scoped>

</style>



